


























import Vue from 'vue'
import { mapState, mapActions } from 'vuex'

interface Imessage {
  message: string,
  id: string
}

export default Vue.extend({
  name: 'Chat',
  props: {
    msg: String
  },

  data: () => ({
    textarea: '',
    messages: [] as Imessage[],
    chatScroll: 0,
    chatHeight: 0
  }),

  computed: {
    ...mapState(['p2p'])
  },

  methods: {
    ...mapActions(['emit']),

    send () {
      const message = {
        message: this.textarea,
        id: this.p2p.peerId
      }
      this.p2p.emit('peer-msg', message)
      this.messages.push(message)

      this.textarea = ''
    },

    onScroll (data) {
      this.chatScroll = data.target.scrollTop
      this.chatHeight = data.target.scrollHeight - data.target.clientHeight
    }
  },

  watch: {
    messages (messages: Imessage) {
      localStorage.messages = JSON.stringify(messages)

      setTimeout(() => {
        this.$refs.chat.scrollTo(0, this.$refs.chat.scrollHeight)
      }, 300)
    }
  },

  mounted () {
    if (localStorage.messages) {
      this.messages = JSON.parse(localStorage.messages)
    }

    this.p2p.on('peer-msg', (data: Imessage) => {
      this.messages.push(data)
    })
  }
})
